<template>
  <section class="order" :class="{fixed: isFixed}">
    <a @click="getAlls" href="javascript:void(0)">综合</a>
    <!--<a href="javascript:void(0)">销量</a>-->
    <a @click="order('create_time')" href="javascript:void(0)">新品</a>
    <a href="javascript:void(0)" @click="clickTypesHandle">分类</a>
  </section>
</template>

<script>
  import {getQueryString} from '../../../lib/util'

  export default {
    props: {
      shopId: [String, Array, Object],
      isFixed: Boolean
    },
    methods: {
      clickTypesHandle () {
        this.$parent.$emit('expand')
      },
      getAlls () {
        this.$parent.$emit('getAllGoods')
      },
      order (type) {
        let order = ''
        if (!this.priceState) {
          this.priceState = !this.priceState
          order = `{"${type}": 1}`
        } else {
          this.priceState = !this.priceState
          order = `{"${type}": 0}`
        }
        this.$router.push(`/shop/${this.shopId}/search?label=${getQueryString('label')}&order=${order}`)
      }
    }
  }
</script>

<style lang="less" scoped>
  .order{
    display: flex;
    height: 40px;
    line-height: 40px;
    background: #fff;
    border-bottom: 1px solid #ddd;
    position: relative;
    width: 100%;
    z-index: 100;
    box-shadow: 0 1px 1px 1px rgba(0,0,0,.05);
    a {
      flex: 1;
      display: block;
      width: 1px;
      text-align: center;
      font-size: 15px;
      color: #999;
    }
  }
  .fixed {
    position: fixed;
    z-index: 1;
    transform: translateZ(0px);
    top: 0px;
    width: 100%;
  }
</style>



