<template>
  <div class="ui-wrap shop-search">
    <!--<search-top-bar-->
      <!--:shop-id="shopId"-->
    <!--/>-->
    <section class="goods">
      <div class="list clearfix">
        <goods-item
          v-for="item in goodsList"
          :key="item.mall_id"
          :goods-detail="item"
        />
      </div>
    </section>
    <mt-popup
      v-model="popupVisible"
      pop-transition="popup-fade"
      position="right"
      class="ios-s"
      style="height: 100%; width: 70%; z-index: 99999; overflow-x: scroll;">
      <div class="rank-content">
        <div class="sel">
          <div class="title" @click="getAll">所有商品</div>
          <input type="text" v-model="keyword" placeholder="商品名称或图文号">
          <a href="javascript:void (0)" @click="search">搜索</a>
        </div>
        <goods-rank
          v-for="item in label"
          :key="item.value"
          :rank="item"
        ></goods-rank>
      </div>
    </mt-popup>
  </div>
</template>

<script>
  import SearchTopBar from './search-top-bar'
  import GoodsItem from '../../mall/goods/goods.item'
  import GoodsRank from '../../mall/goods/goods-label-rank'
  import {ShopUrl} from '../../../config/urls'
  import {getQueryString} from '../../../lib/util'
  import bus from '../../../assets/js/bus'

  export default {
    data () {
      return {
        shopId: '',
        goodsList: [],
        popupVisible: false,
        keyword: '',
        label: []
      }
    },
    created () {
      this.getData()
    },
    watch: {
      '$route' () {
        let queryString = getQueryString('label')
        let order = getQueryString('order')
        let label = 0
        if (queryString) {
          switch (queryString) {
            case 'all':
              this.getData()
              break
            default:
              label = queryString
              this.getData(null, label, order)
              break
          }
        }
      }
    },
    mounted () {
      this.$on('expand', () => {
        this.popupVisible = true
      })

      this.$on('getAllGoods', () => {
        this.getAll()
      })

      bus.$on('selectLabel', v => {
        this.popupVisible = false
        this.$router.push(`/shop/${this.shopId}/search?label=${v.value}`)
      })
    },
    methods: {
      getData (page, label, order, keywords) {
        this.shopId = this.$route.params.id
        this.$http.get(ShopUrl.goods.list, {params: {unit_id: this.$route.params.id, page, unit_label: label, order, keywords}})
          .then(data => {
            this.goodsList = data.data.data.list
          })
          .catch(err => this.$toast('网络错误:' + err.message))

        this.$http.get(ShopUrl.label.list, {params: {unit_id: this.$route.params.id}})
          .then(data => {
            this.label = data.data.data.list
          })
          .catch(err => this.$toast('网络错误:' + err.message))
      },
      getScrollTop () {
        let scrollTop = 0
        if (document.documentElement && document.documentElement.scrollTop) {
          scrollTop = document.documentElement.scrollTop
        } else if (document.body) {
          scrollTop = document.body.scrollTop
        }
        return scrollTop
      },
      search () {
        this.getData(null, null, null, this.keyword)
        this.popupVisible = false
      },
      getAll () {
        this.popupVisible = false
        this.getData()
      }
    },
    components: {
      SearchTopBar,
      GoodsItem,
      GoodsRank
    }
  }
</script>

<style lang="less" scoped>
  .shop-search{
    height: auto;
    min-height: 100%;
    margin-bottom: 80px;
    padding-top: 0;
    background-color: #fff;
  }
  .rank-content{
    padding-bottom: 60px;
    .sel{
      .title{
        background: #e7e7e7;
        padding: 6px 15px;
        border-bottom-color: #eee;
        color: #777;
        border-color: #ddd;
        border-bottom: 1px solid transparent;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
      }
      input{
        display: inline-block;
        height: 20px;
        padding-left: 1rem;
        background-color: transparent;
        font-size: 14px;
        border: 1px solid #ddd;
        border-radius: 0;
        vertical-align: middle;
      }
      a{
        color: #333;
        vertical-align: middle;
      }
    }
  }
</style>



